import React,{useState} from "react";
import { Button, Modal } from 'react-bootstrap';
import img from "../../../../assets/images/userface.jpg";
import './DetailUser.css'

const DetailUser = ({display, setDisplay, data}) => {
    return (
        <>
            <Modal size="lg" show={display} onHide={() => setDisplay(false)} aria-labelledby="example-modal-sizes-title-lg">
                <Modal.Header closeButton>
                    <Modal.Title id="example-modal-sizes-title-lg">
                        y l {data?.fullname}
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <div className="row">
                        <div className="col-sm-6 ">
                            <div className="img-user">
                                <img src={img} alt="img-user"/>
                            </div>
                            <div className="infor-user">
                                <h6>H tn: <span className="text-detail">{data?.fullname}</span></h6>
                                <h6>Tn ng nhp: <span className="text-detail">{ data?.username }</span></h6>
                                <h6>S in thoi: <span className="text-detail">{data?.phone}</span></h6>
                                <h6>Email: <span className="text-detail">{data?.email}</span></h6>
                                
                            </div>
                        </div>
                        <div className="col-sm-6 ">
                            <div className="detail-user">
                                <h6>Gii tnh: <span className="text-detail">{data?.gender}</span></h6>
                                <h6>a ch: <span className="text-detail">{data?.address}</span></h6>
                                <h6>CMND/CCCD/PASSPORT: <span className="text-detail">{data?.passport}</span></h6>
                                <h6>Quc tch: <span className="text-detail">{data?.nationality}</span></h6>
                                <h6>Dn tc: <span className="text-detail">{data?.nation}</span></h6>
                                <h6>Chc v: <span className="text-detail">{data?.position}</span></h6>
                                <h6>Trng thi: <span className="text-detail">{data?.work ? "ang lm vic":"ngh vic"}</span></h6>
                            </div>
                        </div>
                    </div>
                </Modal.Body>
            </Modal>
        </>
    )
}
export default DetailUser;