import React, {useState} from "react";
import PropTypes from 'prop-types';
import * as Yup from 'yup';
// import { useHistory } from 'react-router-dom';
import { Formik, Form, FastField, Field } from 'formik';
import InputField from '../../../Common/CustomFields/InputFields/InputFields';
import AdminApi from "../../../../api/Auth/authApi";

const FormAdd = ({control, userLoad}) => {
    // const [imgPreview, setImgPreview] = useState(null);
    // const [error, setError] = useState(false);
    // // const history = useHistory();
    
    // const uploadImage = (e) => {
    //     const formData = e.target.files[0];
    //     const ALLOWED_TYPES = ["image/png", "image/jpeg", "image/jpg"];
    //     if (formData && ALLOWED_TYPES.includes(formData.type)) {
    //         let reader = new FileReader();
    //         reader.onloadend = () => {
    //             setImgPreview(reader.result)
    //         }
    //         reader.readAsDataURL(formData);
    //     } else {
    //         setError(true)
    //     }
    // };

    const initialValues = {
            fullname: '',
            username: '',
            password: '',
            // image: '',
            phone: '',
            email: '',
            gender: '',
            address: '',
            passport: '',
            nationality: '',
            nation: '',
            position: '',
            work: true
    }

    // const validationSchema =Yup.object().shape({
    //     fullname: Yup.string().required('vui lng nhp tn ngi dng')
    // })

    const onSubmit = async (data) => {
        // console.log(data, 434434342);
        await AdminApi.register({ ...data });
        control(false);
        userLoad();
        // history.push('/admin/quan-tri/danh-sach-quan-tri')
    }


    return (
        <>
            <Formik
                // validationSchema={validationSchema}
                initialValues={initialValues}
                onSubmit={(data) => { onSubmit(data)}}
            >
                {formikProps => {
                    return(
                    <Form>
                        <div className="row">
                            <div className="col-sm-6 ">
                                <div className="">
                                    {/* <div className="card-box-img card-box text-center">
                                        {error && <p className="errorMsg">Chn file nh png, jpeg, jpg</p>}
                                        <div
                                            className="imgPreview"
                                            style={{
                                                background: imgPreview
                                                    ? `url("${imgPreview}") no-repeat center/cover`
                                                    : "#d8dff7"
                                            }}
                                        >
                                            {!imgPreview && (
                                                <>
                                                    <p>Thm nh i din</p>
                                                    <label htmlFor="fileUpload" className="customFileUpload ">Chn nh</label>
                                                    <input name="image" type="file" id="fileUpload" onChange={uploadImage} />
                                                </>
                                            )}
                                        </div>
                                        {imgPreview && (<button className="upload-img-button" onClick={() => setImgPreview(null)}>Xa nh</button>)}
                                    </div> */}
                                </div>
                                <div className="col-sm p-4">
                                    <div className="input-group pad-add ">
                                        <FastField label="H v tn" component={InputField} name="fullname" placeholder="Nhp h tn" />
                                    </div>
                                    <div className="input-group pad-add ">
                                        <FastField label="Tn ng nhp" component={InputField} name="username" placeholder="Nhp tn ng nhp" />
                                    </div>
                                    <div className="input-group pad-add">
                                        <FastField label="S in thoi" component={InputField} name="phone" placeholder="Nhp s in thoi" />
                                    </div>
                                    <div className="input-group pad-add ">
                                        <FastField label="Mt khu" component={InputField} name="password" placeholder="Nhp Mt khu" />
                                    </div>
                                    <div className="input-group pad-add">
                                        <FastField label="Email" component={InputField} name="email" placeholder="Nhp email" />
                                    </div>
                                    <div className="input-group pad-add">
                                        <label className="label-field">Gii tnh</label>
                                        <Field label="" name="gender" as="select" className="form-group-table form-select">
                                                <option label="Nhp gii tnh" />
                                                <option value="Nam" label="Nam" />
                                                <option value="N" label="N"/>
                                        </Field>
                                    </div>  
                                </div>
                            </div>
                            <div className="col-sm-6 ">
                                <div className="col-sm p-4">
                                          
                                    <div className="input-group pad-add">
                                        <FastField label="a ch" component={InputField} name="address" placeholder="Nhp a ch" />
                                    </div>
                                    <div className="input-group pad-add">
                                        <FastField label="CMND/CCCD/PASSPORT" component={InputField} name="passport" placeholder="Nhp s CMND/CCCD/PASSPORT" />
                                    </div>
                                    <div className="input-group pad-add">
                                        <FastField label="Quc tch" component={InputField} name="nationality" placeholder="Nhp quc tch" />
                                    </div>
                                    <div className="input-group pad-add">
                                        <FastField label="Dn tc" component={InputField} name="nation" placeholder="Nhp dn tc" />
                                    </div>
                                    <div className="input-group pad-add">
                                        <FastField label="Chc v" component={InputField} name="position" placeholder="Nhp chc v" />
                                    </div>
                                    <div className="input-group pad-add">
                                        <label className="label-field">Trng thi</label>
                                        <Field label="" name="work" as="select" className="form-group-table form-select mb-3">
                                                <option label="Nhp trng thi" />
                                                <option value={true} label="ang lm vic" />
                                                <option value={false} label="Ngh lm"/>
                                        </Field>
                                    </div>    
                                </div>
                            </div>
                            <div className="auth-button-footer pad-4">
                                <button type="reset" className="btn btn-danger mr-5">Xa</button>
                                <button type="submit" className="btn btn-primary ">Lu</button>
                            </div>
                        </div>
                    </Form>
                     ) }}
            </Formik>
        </>
    )
}
FormAdd.propTypes = {
    onsubmit: PropTypes.func
}
FormAdd.defaultProps = {
    onsubmit: null
}

export default FormAdd;