export const CheckWord = (text) => {
    let valueInput = text.toLowerCase()
    let arr = ['nng' , 'loz', 'lolz', 'lone', 'lz' ,'ln' ,
    'Ln' ,'' , '' , '' , 'cc', 'cc' , 'ncc', 'fuck',
    'Fuck','bitch', 'Bitch', '' , '' , 'm' , 'm' , 'M' ,
    'dm' ,'Dm' , 'DM' ,'mm' , 'mm' ,'dmm' ,'Dmm' ,'cl' , 'clm' ,
        'clmm', 'clgt', 'Clgt', 'o', 'o', "lon", "vai", "vl"]
    
    arr.forEach(word => {
        if (valueInput.includes(word)) {
            let str = ""
            for (let i = 0; i < word.length; i++){
                str += "*"
            }
            valueInput= valueInput.replace(word, str)
        }
    })
    return valueInput
}

export const  removeAccents = (str)  => {
    var AccentsMap = [
        "a",
        "A",
        "d", "D",
        "e",
        "E",
        "i",
        "I",
        "o",
        "O",
        "u",
        "U",
        "y",
        "Y",
    ];
    for (var i=0; i<AccentsMap.length; i++) {
        var re = new RegExp('[' + AccentsMap[i].substr(1) + ']', 'g');
        var char = AccentsMap[i][0];
        str = str.replace(re, char);
    }
    return str;
}