import React from 'react';
import PropTypes from 'prop-types';
import { Formik, Form, FastField } from 'formik';
import InputField from '../../../../Common/CustomFields/InputFields/InputFields';
import MyTextArea from '../../../../Common/CustomFields/TextAreaFields/TextAreaFields';
import SendApi from '../../../../../api/Send/sendApi';
import './FormSend.css';

const FormAdd = () => {

    const initialValues = {
        name: '',
        address: '',
        phone: '',
        text: '',
        clothes: '',
        car: '',
        mark: '',
        vegetable: '',
        rice: '',
        noodles: '',
        milk: '',
        egg:'',
        status: false
    }

    const onSubmit = async (data) => {
        console.log(data);
        await SendApi.addSend({...data})
    }

    return (
        <>
            <section className="section">
                {/* <div className="card"> */}
                    <div className="body-form-add">
                    <Formik
                        initialValues={initialValues}
                        onSubmit={(data)=>{onSubmit(data)}}
                    >
                            {formikProps => {
                                return ( 
                                    <Form>
                                        <div className="input-group">
                                            <FastField label="o qun" component={InputField} name="clothes" placeholder="Nhp s b" />
                                        </div>
                                        <div className="input-group">                                           
                                            <FastField label="Khu trang" component={InputField} name="mark" placeholder="Nhp s hp" />
                                        </div>
                                        <div className="input-group">                                          
                                            <FastField label="Rau c qu" component={InputField} name="vegetable" placeholder="Nhp s kg" />
                                        </div>
                                        <div className="input-group">                                          
                                            <FastField label="Go" component={InputField} name="rice" placeholder="Nhp s kg" />
                                        </div>
                                        <div className="input-group">                                          
                                            <FastField label="M gi" component={InputField} name="noodles" placeholder="Nhp s thng" />
                                        </div>
                                        <div className="input-group">                                         
                                            <FastField label="Sa" component={InputField} name="milk" placeholder="Nhp s hp" />
                                        </div>
                                        <div className="input-group">                                          
                                            <FastField label="Trng" component={InputField} name="egg" placeholder="Nhp s qu" />
                                        </div>
                                        <div className="input-group">                                        
                                            <FastField label="Xe ti" component={InputField} name="car" placeholder="Nhp s chic" />
                                        </div>
                                        <h5>Thng tin lin lc</h5>
                                        <div className="input-group">
                                            <FastField label="H v tn" component={InputField} name="name" placeholder="Nhp h v tn" />
                                        </div>
                                        <div className="input-group">
                                            <FastField label="a ch" component={InputField} name="address" placeholder="Nhp a ch" />
                                        </div>
                                        <div className="input-group">
                                            <FastField label="S in thoi" component={InputField} name="phone" placeholder="S in thoi" />
                                        </div>
                                        <div style={{display: 'none'}}>
                                            <FastField component={InputField} name="status" placeholder="S in thoi" />
                                        </div>
                                        <MyTextArea label="Ghi ch" name="text"/>
                                        <div className="auth-button-footer ">
                                            <button type="reset" className="btn btn-danger mr-5">Xa</button>
                                            <button type="submit" value="submit" className="btn btn-primary ">Lu</button>
                                        </div>
                                    </Form>
                                )
                            }}
                         </Formik>
                    </div>
                {/* </div> */}
            </section>
        </>
    )
}

FormAdd.propTypes = {
    onSubmit: PropTypes.func
}
FormAdd.defaultProps = {
    onSubmit: null,
}
export default FormAdd;